<template>
    <el-dialog title="edit"  v-model="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="CompanyCode:" :label-width="formLabelWidth"  :required="true">
                <el-input v-model.trim="form.CompanyCode" auto-complete="off" size="small"></el-input>
            </el-form-item>
            <el-form-item label="EmpCode:" :label-width="formLabelWidth"  :required="true">
                <el-input v-model.trim="form.EmpCode" auto-complete="off" size="medium"></el-input>
            </el-form-item>
            <el-form-item label="EmploymentStartDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model.trim="singleDate"
                        type="date"
                        placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="EmploymentEndDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model.trim="singleEndDate"
                        type="date"
                        placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="EmploymentType:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.EmploymentType" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="SurName:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.SurName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="OtherName:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.OtherName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="FullChineseName:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.FullChineseName" auto-complete="off"></el-input>
            </el-form-item>
            <!--<el-form-item label="Gender:" :label-width="formLabelWidth">-->
                <!--<el-select-->
                        <!--size="small"-->
                        <!--v-model="form.Gender" >-->
                    <!--<el-option-->
                            <!--v-for="(item, $key) in Gender"-->
                            <!--:value="item.value"-->
                            <!--:label="item.name"-->
                            <!--:key="$key">-->
                    <!--</el-option>-->
                <!--</el-select>-->
            <!--</el-form-item>-->
            <el-form-item label="MobilePhone:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.MobilePhone" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="HomePhone:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.HomePhone" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="BirthDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model.trim="singleBirthDate"
                        type="date"
                        placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="Email:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Email" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="ICN:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.ICN" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="Passport:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Passport" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="Country:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Country" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="MaritalStatus:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.MaritalStatus" >
                    <el-option
                            v-for="(item, $key) in MaritalStatus"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="SpouseName:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.SpouseName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="SpouseICN:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.SpouseICN" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="SpousePassport:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.SpousePassport" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="SpouseCountry:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.SpouseCountry" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="ResidentialAddress:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.ResidentialAddress" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="PostalAddress:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.PostalAddress" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="BankAccount:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.BankAccount" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="EmpTaxFileNum:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.EmpTaxFileNum" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="Title:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Title" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="PaySlipDeliveryDay:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.PaySlipDeliveryDay" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="IsActive:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.IsActive" auto-complete="off" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="IsPartime:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.IsPartime" >
                    <el-option
                            v-for="(item, $key) in IsPartime"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="CreateDate:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.CreateDate" auto-complete="off" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="UpdateDate:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.UpdateDate" auto-complete="off" :disabled="true"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
.updatePwd{
    .el-dialog__header{
        padding:30px;
        .el-dialog__headerbtn{
            width:50px;
            height:50px;
            margin:-30px -30px 0 0;
        }
    }
    .el-dialog__body{
        padding:30px 50px 0 0;
    }
}
</style>

<script>
import QuRequest from '../../../core/QuRequest'

const quRequest = new QuRequest()

export default {

    data() {
        return {
            Gender:[
                { 'name': 'man', 'value': true},
                { 'name': 'women','value': false},
            ],
            IsPartime:[
                { 'name': 'yes', 'value': true},
                { 'name': 'no','value': false},
            ],
            MaritalStatus:[
                { 'name': 'no','value': 0},
                { 'name': 'yes', 'value': 1},
            ],
            titleShow:false,
            dialogForm: false,
            formLabelWidth: '150px',
            disabled:true,
            isShow:true,
            form:{
                CompanyCode:'',
                EmpCode: '',
                EmploymentStartDate:'',
                EmploymentEndDate:'1',
                EmploymentType:'',
                SurName:'',
                OtherName:'',
                FullChineseName:'',
                Gender:'',
                MobilePhone:'',
                HomePhone:'',
                BirthDate:'',
                Email:'',
                ICN:'',
                Passport:'',
                Country:'',
                MaritalStatus:'1',
                ResidentialAddress:'',
                PostalAddress:'',
                BankAccount:'',
                Title:'',
                PaySlipDeliveryDay:'',
                SpouseCountry:'',
                CreateDate:'',
                SpouseName:'',
                SpouseICN:'',
                SpousePassport:'',
                IsPartime:'',
                IsActive:'',
                UpdateDate:'',
            }
        }
    },
    computed: {

        singleDate: {
            set (newValue) {
                this.form.EmploymentStartDate = this.formatDate(newValue)
            },
            get () {
                return this.form.EmploymentStartDate
            }
        },
        singleEndDate: {
            set (newValue) {
                this.form.EmploymentEndDate = this.formatDate(newValue)
            },
            get () {
                return this.form.EmploymentEndDate
            }
        } ,
        singleCreateDate: {
            set (newValue) {
                this.form.CreateDate = this.formatDate(newValue)
            },
            get () {
                return this.form.CreateDate
            }
        },
        singleBirthDate: {
            set (newValue) {
                this.form.BirthDate = this.formatDate(newValue)
            },
            get () {
                return this.form.BirthDate
            }
        }

    },
    mounted() {

    },
    methods: {
        formatDate(dateStr){
            let date = new Date(dateStr);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            let d = date.getDate();
            return [y, this.addZero(m), this.addZero(d)].join('/');
        },
        addZero: function (num) {
            return (num > 9 ? '' : '0') + num
        },
        showDialog(item) {
            if(item){
                this.isShow = false;
                this.form.CompanyCode= item.CompanyCode;
                this.form.EmpCode =item.EmpCode;
                this.form.EmploymentStartDate=item.EmploymentStartDate;
                this.form.EmploymentType =item.EmploymentType;
                this.form.SurName= item.SurName;
                this.form.OtherName =item.OtherName;
                this.form.FullChineseName=item.FullChineseName;
                this.form.Gender =item.Gender;
                this.form.MobilePhone= item.MobilePhone;
                this.form.HomePhone =item.HomePhone;
                this.form.BirthDate=item.BirthDate;
                this.form.Email =item.Email;
                this.form.ICN= item.ICN;
                this.form.Passport =item.Passport;
                this.form.Country=item.Country;
                this.form.MaritalStatus =item.MaritalStatus;
                this.form.ResidentialAddress= item.ResidentialAddress;
                this.form.PostalAddress =item.PostalAddress;
                this.form.BankAccount=item.BankAccount;
                this.form.Title =item.Title;
                this.form.PaySlipDeliveryDay=item.PaySlipDeliveryDay;
                this.form.CreateDate =item.CreateDate;
                this.form.SpouseCountry=item.SpouseCountry;
                this.form.SpouseICN =item.SpouseICN;
                this.form.SpouseName=item.SpouseName;
                this.form.SpousePassport =item.SpousePassport;
                this.form.UpdateDate =item.UpdateDate;
                this.titleShow = false;
            }else{
                this.titleShow = true;
                this.form.platform= '1';
                this.form.version='';
                this.form.info ='';
                this.form.pkg_id = '';
            }

            this.dialogForm = true;
        },
        submit(){
            quRequest.send(quRequest.Apis.qutuiba.member.update,{'json':JSON.stringify(this.form)}
            ).then(res => {
                this.$message.success("提交成功！");
                this.dialogForm = false;
                this.$emit('update')
            }).catch(err => {
                this.$message.error(err.message);
            }).fin(() => {
                this.loading = false;
            }).done();
        }
    }
}
</script>


